@import url(https://fonts.googleapis.com/css?family=Kameron|Orbitron);


body {
  margin: 0;
  font-family: "Kameron", serif;
  color: white;
  min-height: 100vh;
  background: -webkit-linear-gradient(190deg, #034f79, #067577);
  background: linear-gradient(260deg, #034f79, #067577);
  display: flex;
  flex-direction: column-reverse;
}

#App {
  display: flex;
  flex-direction: column;
}

header {
  padding: 0 0 16px 0;
}

main {
  height: 70vh;
}

h1 {
  font-family: "Orbitron", sans-serif;
  font-size: 3rem;
  margin: 10px;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 1);
  color: #17e7e0;
}

h1 + p {
  font-size: 1.2rem;
}

#App header {
  text-align: center;
}

#Modal {
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.modalContent {
  border-radius: 30px;
  padding: 10px;
  border: 5px solid rgba(0, 0, 0, 0.5);
  max-width: 500px;
  width: 80%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: -webkit-linear-gradient(190deg, #034f79, #067577);
  background: linear-gradient(260deg, #034f79, #067577);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.modalContent img {
  border: 1px dotted white;
  border-radius: 50%;
}

.modalContent h2 {
  margin: 5px 5px 0px 5px;
}

.modalContent .title {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  align-self: center;
}

.modalContent .details {
  align-self: flex-start;
  text-transform: capitalize;
  border: 1px solid white;
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  line-height: 1.5em;
}

.modalContent button {
  background: transparent;
  font-size: 1.2rem;
  padding: 0px 15px;
  font-family: "Kameron", serif;
  color: white;
  oultine: none;
  border: 2px solid white;
  border-radius: 10px;
}

.on {
  display: flex;
}

.off {
  display: none;
}

.Card {
  background: -webkit-linear-gradient(
    40deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0)
  );
  background: linear-gradient(
    50deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0)
  );
  text-align: center;
  padding: 12px;
  border: 2px solid white;
  border-radius: 7px;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.5);
  -webkit-filter: sepia(75%);
          filter: sepia(75%);
  -webkit-transition: -webkit-transform 500ms, -webkit-filter 500ms;
  transition: -webkit-transform 500ms, -webkit-filter 500ms;
  transition: transform 500ms, filter 500ms;
  transition: transform 500ms, filter 500ms, -webkit-transform 500ms, -webkit-filter 500ms;
}

.Card:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  -webkit-filter: sepia(0%);
          filter: sepia(0%);
}

.Card h2 {
  padding-bottom: 3px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.Card p {
  font-style: italic;
}

.Card img {
  border: 2px dotted white;
  border-radius: 50%;
}

#CardList {
  padding: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 15px;
}

